























































import 'reflect-metadata'
import { Vue, Component, Prop } from 'vue-property-decorator'

import { AvaNetwork } from '@/js/AvaNetwork'
import axios from 'axios'
import punycode from 'punycode'

@Component
export default class CustomPage extends Vue {
    name = 'My Custom Network'
    url = ''
    explorer_api = ''
    explorer_site = ''
    err: null | string = null
    err_url = ''
    isAjax = false

    cleanExplorerUrl() {
        let url = this.explorer_api
        url = punycode.toASCII(url)
        this.explorer_api = url
    }

    cleanExplorerSite() {
        let url = this.explorer_site
        url = punycode.toASCII(url)
        this.explorer_site = url
    }

    checkUrl() {
        let err = ''
        let url = this.url
        // protect against homograph attack: https://hethical.io/homograph-attack-using-internationalized-domain-name/

        url = punycode.toASCII(url)
        this.url = url

        // must contain http / https prefix
        if (url.substr(0, 7) !== 'http://' && url.substr(0, 8) !== 'https://') {
            this.err_url = 'URLs require the appropriate HTTP/HTTPS prefix.'
            return false
        }

        let split = url.split('://')
        let rest = split[1]

        // must have base ip
        if (rest.length === 0) {
            this.err_url = 'Invalid URL.'
            return false
        }

        // Must have port
        if (!rest.includes(':')) {
            this.err_url = 'You must specify the port of the url.'
            return false
        }

        // Port must be number
        let urlSplit = rest.split(':')
        if (urlSplit.length === 0) {
            this.err_url = 'Invalid port.'
            return false
        }

        let port = parseInt(urlSplit[1])

        if (isNaN(port)) {
            this.err_url = 'Invalid port.'
            return false
        }

        this.err_url = ''
        return true
    }
    errCheck() {
        let err = null

        // check for HTTP HTTPS on url
        let url = this.url

        if (url.substr(0, 7) !== 'http://' && url.substr(0, 8) !== 'https://') {
            err = 'URLs require the appropriate HTTP/HTTPS prefix.'
        }

        if (!this.name) err = 'You must give the network a name.'
        else if (!this.url) err = 'You must set the URL.'

        return err
    }
    async submit() {
        this.err = null
        let err = this.errCheck()

        if (err) {
            this.err = err
            return
        }

        this.isAjax = true
        let netID = null

        try {
            let resp = await axios.post(this.url + '/ext/info', {
                jsonrpc: '2.0',
                id: 1,
                method: 'info.getNetworkID',
            })
            netID = resp.data.result.networkID
            this.isAjax = false
        } catch (e) {
            this.isAjax = false
            this.err = 'Muellners Network Not Found'
            return
        }

        let net = new AvaNetwork(this.name, this.url, netID, this.explorer_api, this.explorer_site)

        this.$emit('add', net)

        // Clear values
        this.name = 'My Custom Network'
        this.url = ''
    }
}
